import React from "react";
import { getAssetURL } from "~/utils/get-asset-url";
import menuLogo from "~/assets/images/foodplaceLogo.png";
import {
  DIETARY_PREFERENCES_ENG,
  DIETARY_PREFERENCES_DK,
  ALLERGENS_ENG,
  ALLERGENS_ENG_DK,
  EMPTY_ALLERGENS_ENG,
  EMPTY_ALLERGENS_DK
} from "~/utils/constants.js";
function TabContent({
  menuDk,
  menuEng,
  Allergens,
  Dietary,
  stationName,
  plateImg,
  copyText,
  language,
  isAdminUser,
  calorie,
  co2,
  showType,
  stationImg,
  firstStation
}) {
  const menu = language === "da" ? menuDk : menuEng;
  const TotalCo2 =
    co2 !== null && co2 !== undefined ? Number(co2)?.toFixed(2) : null;
  const TotalCalorie =
    calorie !== null && calorie !== undefined
      ? Number(calorie)?.toFixed(2)
      : null;
      
  return (
    <div
      className={`${firstStation? 'bg-velux' :'bg-velux'} tab-preview relative w-full min-h-[100vh] flex justify-start items-center`}
      key={Math.random()}
    >
      <div className="banner-right absolute top-0 right-0">
        <div
          className="banner-img flex items-center"
          style={{
            backgroundImage: `url(${
              plateImg != null && plateImg !== undefined && plateImg !== ""
                ? showType == "id" || showType == undefined
                  ? getAssetURL(plateImg)
                  : plateImg
                : null
            })`,
          }}
        ></div>
      </div>
      <div className="w-full text-white relative flex flex-col px-16 py-10 font-noto full-content content-area">
        <div className="banner-left w-full self-center text-left">
          {menu?.trim()?.length > 0 && (
            stationImg ?
            <div className="station-image"> 
            <img 
              src={getAssetURL(stationImg)} 
              alt="station image" 
              className="pb-4 max-w-[180px] min-h-[100px]" 
            /> 
        </div> 
            :
          <div className="station-name pb-6 block text-left w-full font-noto font-normal uppercase">
            {stationName}
          </div>
          )}
          {<div className="banner-menu">{menu}</div>}
          {menu?.trim()?.length > 0 && (
            <div className="banner-allergen font-normal">
              <span className="font-bold">
                {language === "da" ? ALLERGENS_ENG_DK : ALLERGENS_ENG}:{" "}
              </span>
              {Allergens?.length > 0
                ? Allergens?.map(
                    (e, i) =>
                      `${
                        e?.Allergens_id?.[
                          language === "da" ? "name" : "translated_name"
                        ]
                      }${i !== Allergens?.length - 1 ? ", " : ""}`
                  )
                : language === "da"
                ? EMPTY_ALLERGENS_DK
                : EMPTY_ALLERGENS_ENG}
            </div>
          )}
          {menu?.trim()?.length > 0 && Dietary?.length > 0 && (
            <div className="banner-dietary font-normal">
              <span className="font-bold">
                {language === "da"
                  ? DIETARY_PREFERENCES_DK
                  : DIETARY_PREFERENCES_ENG}
                :{" "}
              </span>
              {Dietary?.map(
                (e, i) =>
                  `${
                    e?.Dietary_id?.[
                      language === "da" ? "name" : "translated_name"
                    ]
                  }${i !== Dietary?.length - 1 ? ", " : ""}`
              )}
            </div>
          )}
          {menu?.trim()?.length > 0 && (
            <div className="banner-calorie-co2 grid grid-cols-12 mt-16 mx-auto">
              {TotalCo2 !== null && TotalCo2 > 0 && (
                <div className={`flex ${firstStation ? 'go-green':'col-span-6'}  align-middle`}>
                  <div className="value  min-w-[122px] min-h-[122px] max-w-[122px] max-h-[122px] bg-[#485648] rounded-full flex justify-center	items-center p-[20px] font-medium	text-3xl 3xl:text-4xl flex-col">
                    {TotalCo2} <span className="text-xl">kg</span>
                  </div>
                  <div className="content flex justify-start items-center font-medium ml-[7%] text-2xl">
                    CO2
                  </div>
                </div>
              )}
              {TotalCalorie !== null && TotalCalorie > 0 && (
                <div className={`flex  ${firstStation ? 'go-green':'col-span-6'} align-middle`}>
                  <div className="value min-w-[122px] min-h-[122px] max-w-[122px] max-h-[122px] bg-[#485648] rounded-full flex justify-center	items-center p-[20px] font-medium	text-3xl 3xl:text-4xl flex-col">
                    {TotalCalorie} <span className="text-xl">kcal</span>
                  </div>
                  <div className="content flex justify-start items-center font-medium ml-[7%] text-2xl">
                    {language === "da" ? "Kalorier" : "Calories"}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
      <div className="absolute bottom-10 right-5">
        <div
          className="footer-icon-img flex items-center"
        ><img src={menuLogo} alt="" /></div>
      </div>
      {isAdminUser && (
        <div className="absolute bottom-5 left-5 text-white bg-black p-2">
          <button onClick={() => copyText({ language: language, type: 1 })}>
            Copy Link <i className="ri-clipboard-line"></i>
          </button>
        </div>
      )}
    </div>
  );
}

export default TabContent;
